import { Component, OnInit } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';

@Component({
  selector: 'app-jsonadd',
  templateUrl: './jsonadd.component.html',
  styleUrls: ['./jsonadd.component.css']
})
export class JsonaddComponent implements OnInit {

  constructor(private http: Http) { }
  productObj:object = {};
  products=[];
  addNewProduct = function(product){
    this.productObj = {
      "name": product.name,
      "age" : product.age
    }
    console.log(product.name);
    this.http.post("http://localhost:5555/products/", this.productObj).subscribe(      
          (res : Response)=>{
            this.fetchData();
              console.log(res);
          }
        )

  }
  fetchData = function(){
    this.http.get("http://localhost:5555/products").subscribe(

    (res : Response)=>{
        this.products=res.json();
       
       
    }
  )

  }
  ngOnInit() {
  }

}



