import { Component, OnInit } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Console } from '@angular/core/src/console';

import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'app-test-json',
  templateUrl: './test-json.component.html',
  styleUrls: ['./test-json.component.css']
})
export class TestJsonComponent implements OnInit {
  products = [];
  test ="abcd";
  updateClick=false;
  constructor(private https: Http) { }
  id: number;
  private headers = new Headers({ 'Content-Type':'application/json'});
  productObj:object = {};
  singleproductObj = {};
  
  addNewProduct = function(product){
    this.productObj = {
      "name": product.name,
      "age" : product.age
    }
    console.log(product.name);
    this.https.post("http://localhost:5555/products/", this.productObj).subscribe(      
          (res : Response)=>{
            this.fetchData();
              console.log(res);
          }
        )

  }
  
  fetchData = function(){
    this.https.get("http://localhost:5555/products").subscribe(

    (res : Response)=>{
        this.products=res.json();

    }
  )

  }
  deleteData = function(id){
   if(confirm("Are you sure to delete?")){
const url = "http://localhost:5555/products/"+id;



return this.https.delete(url, {headers: this.headers}).toPromise().then(() =>{
this.fetchData();

})

}

  }

  fetchDataForParticularId = function(id){
    this.https.get("http://localhost:5555/products").subscribe(

    (res : Response)=>{
        this.products=res.json();

        for (var i=0;i<this.products.length;i++){
          console.log(this.products[i].id);
          if(this.products[i].id==id){
            this.singleproductObj = {
              "name": this.products[i].name,
              "age" : this.products[i].age,
              "id" : this.products[i].id
            }
          }
        }
       
       
    }
  )
  this.updateClick=true;

  } 
  updateOldProduct = function(product){
    this.updateClick=false;
    this.productObj = {
      "name": product.name,
      "age" : product.age
      
    }
    
    const url = "http://localhost:5555/products/"+product.id;
    
    console.log(url);
    
   return this.https.put(url,JSON.stringify(this.productObj) ,{headers: this.headers}).toPromise().then(() =>{
    this.fetchData();
    
   })
   
  }

  ngOnInit() {
    this.fetchData();
  }

}



